import React, { useState } from "react";
import { ChakraProvider, Grid, GridItem, Box } from "@chakra-ui/react";
import { Search } from "./homePage/Search";
import { Popular } from "./homePage/Popular";
import { VideoPlayer } from "./homePage/VideoPlayer";
import { useFetch } from "../hooks/useFetch";
import { MusicContext } from "../context/MusicContext";



export const PrincipalScreen = () => {

  const [music, setMusic] = useState({});

  return (
    <>
    <MusicContext.Provider value={{
      music,
      setMusic
    }}>
      <Grid
        templateRows="repeat(2, 1fr)"
        templateColumns="repeat(3, 1fr)"
        gap={5}
        // mt='50px'
        h='95vh'
        mt='2.5vh'
      >
        <GridItem colSpan={2} bg="papayawhip" p={0} >
          <VideoPlayer />
        </GridItem>
        
        <GridItem colSpan={1} rowSpan={2} bg="#1d1c23" borderRadius='30px'>
          <Search />
        </GridItem>

        <GridItem rowSpan={1} colSpan={1} bg="tomato">
          <Popular />
        </GridItem>

        <GridItem rowSpan={1} colSpan={1} bg="tomato">

        </GridItem>
      </Grid>
      </MusicContext.Provider >
    </>
  );
};
